.Layout {
  height: 100vh;
  width: 75vw;
  position: fixed;
  top: 0;
  left: 0;
  background: -webkit-gradient(linear, left bottom, left top, from(var(--dark-white)), to(var(--white)));
  background: -webkit-linear-gradient(bottom, var(--dark-white), var(--white));
  background: -o-linear-gradient(bottom, var(--dark-white), var(--white));
  background: linear-gradient(to top, var(--dark-white), var(--white));
  overflow-y: auto;
}

.Layout::-webkit-scrollbar {
  display: none;
  width: 7px;
}

.Layout::-webkit-scrollbar-track {
  background-color: #5252edb7;
}

.Layout::-webkit-scrollbar-thumb {
  background-color: var(--prestige-blue);
}

.mainContent {
  height: 100%;
}



