.NavItem {
  display: block;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  cursor: pointer;
  -webkit-transition: border 0.2s ease, -webkit-transform 0.2s ease;
  transition: border 0.2s ease, -webkit-transform 0.2s ease;
  -o-transition: transform 0.2s ease, border 0.2s ease;
  transition: transform 0.2s ease, border 0.2s ease;
  transition: transform 0.2s ease, border 0.2s ease, -webkit-transform 0.2s ease;

  /* Layout */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.NavItem:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  border: 1px solid var(--blue);
}

.NavItem:active {
  -webkit-transform: scale(0.7);
      -ms-transform: scale(0.7);
          transform: scale(0.7);
}

.NavItem img {
  width: 50%;
  height: auto;
}

.active {
  border: 2px solid var(--blue);
}



