.Skills {
  height: 0;
  color: white;
  background-color: var(--black);
  background-image: url("https://images.pexels.com/photos/2653362/pexels-photo-2653362.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  background-repeat: no-repeat;
  background-blend-mode: lighten;
  background-position-x: -50px;
  background-attachment: fixed;
  overflow: hidden;
  -webkit-transition: height 0.3s ease, padding 0.3s ease;
  -o-transition: height 0.3s ease, padding 0.3s ease;
  transition: height 0.3s ease, padding 0.3s ease;
}

.Skills h1 {
  font-size: 5rem;
  color: var(--light-gray);
}

.shown {
  height: 100%;
  padding: 40px;
}



