.Side {
  height: 100vh;
  width: 25vw;
  position: fixed;
  top: 0;
  right: 0;
  background-color: var(--black);
  text-align: center;
  z-index: 500;

  /* Layout */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.downSection {
  cursor: pointer;
  color: var(--white);
  opacity: 0;
  -webkit-animation: goDown 0.5s ease 2s reverse forwards;
          animation: goDown 0.5s ease 2s reverse forwards;
}

.downSection:hover .downIcon {
  -webkit-transform: translateY(5px);
      -ms-transform: translateY(5px);
          transform: translateY(5px);
}

.downIcon {
  margin-top: 10px;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  -o-transition: transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.goDown {
  cursor: default;
  -webkit-animation: goDown 0.7s ease-out forwards;
          animation: goDown 0.7s ease-out forwards;
}

@-webkit-keyframes goDown {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}

@keyframes goDown {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}



