.IntroItems {
  width: 75vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  padding: 70px;
  text-align: center;
  padding-top: 180px;
  -webkit-transition: all 0.8s ease-out;
  -o-transition: all 0.8s ease-out;
  transition: all 0.8s ease-out;
}

.IntroItems * {
  opacity: 0;
}

.IntroItems h3 {
  font-size: 2rem;
  font-weight: 400;
  -webkit-animation: show 0.8s ease forwards;
          animation: show 0.8s ease forwards;
}

.IntroItems h1 {
  font-size: 4rem;
  font-weight: 300;
  margin-top: 50px;
  -webkit-animation: show 1s ease 0.9s forwards;
          animation: show 1s ease 0.9s forwards;
}

.IntroItems p {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--light-gray);
  margin-top: 20px;
  -webkit-animation: show 0.9s ease 1.4s forwards;
          animation: show 0.9s ease 1.4s forwards;
}

.goDown {
  width: 25vw;
  left: 75vw;
  padding: 210px 0 0 3%;
  color: var(--white);
  -webkit-animation: align-text 0s linear 1s forwards;
          animation: align-text 0s linear 1s forwards;
}

.goDown h3 {
  font-size: 1.2rem;
}

.goDown h1 {
  font-size: 2.7rem;
}

.goDown p {
  font-size: 1.1rem;
  color: var(--light-gray);
}

@-webkit-keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes align-text {
  from {
    text-align: center;
  }
  to {
    text-align: left;
  }
}

@keyframes align-text {
  from {
    text-align: center;
  }
  to {
    text-align: left;
  }
}



