.Work {
  height: 0;
  background: var(--blue);
  overflow: hidden;
  -webkit-transition: height 0.3s ease, padding 0.3s ease;
  -o-transition: height 0.3s ease, padding 0.3s ease;
  transition: height 0.3s ease, padding 0.3s ease;
}

.shown {
  height: 100%;
}

.slide {
  height: 100%;
  background-color: var(--dark-gray);

  /* Layout */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.slide h1 {
  font-size: 7rem;
  color: var(--light-gray);
}

.show {
  -webkit-animation: go 1s cubic-bezier(0.24, -0.57, 1, -0.03) 1.5s forwards;
          animation: go 1s cubic-bezier(0.24, -0.57, 1, -0.03) 1.5s forwards;
}

@-webkit-keyframes go {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

@keyframes go {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}



