.Navigation {
  width: 90%;
  height: 100px;
  position: absolute;
  bottom: 0;
  left: 5%;
  overflow: hidden;
  z-index: 500;

  -webkit-transform: translateY(100%);

      -ms-transform: translateY(100%);

          transform: translateY(100%);
  opacity: 0;

  -webkit-transition: opacity 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44),
    -webkit-transform 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44);

  transition: opacity 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44),
    -webkit-transform 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44);

  -o-transition: opacity 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44),
    transform 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44);

  transition: opacity 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44),
    transform 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44);

  transition: opacity 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44),
    transform 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44),
    -webkit-transform 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44);
  -webkit-transition-delay: 2s;
       -o-transition-delay: 2s;
          transition-delay: 2s;

  /* Layout */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.goDown {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.active {
  border: 2px solid var(--blue);
}



